<template>
  <div id="app" class="container-fluid">
    <div class="row justify-content-md-center align-items-center">
      <div class="col-sm-12 col-md-8">
        <app-drop-down-list id="select1" label="Select a group" :listItem="listItem"></app-drop-down-list>
        <div class="clearfix">
          <button :disabled="!isReset" v-on:click="spin()" class="btn btn-primary float-left">Spin</button>
          <button v-on:click="reset()" class="btn btn-warning float-right">Reset</button>
        </div>
        <br>
        <app-wheel ref="wheel"></app-wheel>
      </div>
    </div>
  </div>
</template>

<script>
  import DropDownList from './components/DropDownList.vue';
  import Wheel from './components/Wheel.vue';
  export default {
    name: 'app',
    components: {
      appDropDownList: DropDownList,
      appWheel: Wheel
    },
    data(){
      return {
        listItem: [
          {name: 'English Premier League', value: 1},
          {name: 'La Liga', value: 2},
          {name: 'Serie A', value: 3}
        ],
        isReset: true
      }
    },
    methods: {
      spin(){
        this.$refs.wheel.startSpin();
        this.isReset = false;
      },
      reset(){
        this.$refs.wheel.resetWheel();
        this.isReset = true;
      }
    }
  }
</script>

<style>

</style>



